<template>
  <AppHeader></AppHeader>
  <div class="login">
    <div class="login__car">
      <div class="car_box">
        <img src="../assets/login/car.png" alt="car">
      </div>
      <span>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</span>
    </div>
    <div class="login__form_box">
      <div class="login__form_box-alt">
        <form action="" class="login__form">
          <h2>Login Form</h2>
          <p>Hello! Nice to see you again.</p>
          <TextInput
            @emitTextInput="emailModelChange"
            label="Email address"
            placeholder="Type email here..."
            input-id="emailAuth"
            :invalid="validation.loginEmail"
            ></TextInput>
          <PasswordInput
            @emitTextInput="passModelChange"
            :showBtn="true" label="Password"
            placeholder="Type password here..."
            input-id="passwordAuth"
            :invalid="validation.loginPassword"
            ></PasswordInput>
          <router-link to="/forget" class="login_route_link">Forget your password?</router-link>
          <AppButton
            text="sign in"
            :show-icon="true"
            :type="'danger'"
            @action="auth"
            ></AppButton>
          <AppButton text="contact support" :type="'default'"></AppButton>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'

import AppHeader from '@/components/AppHeader.vue'
import TextInput from '../components/TextInput.vue'
import PasswordInput from '../components/PasswordInput.vue'
import AppButton from '@/components/AppButton.vue'

export default {
  data () {
    return {
      emailModel: '',
      passModel: ''
    }
  },
  methods: {
    ...mapMutations(['login', 'logout']),
    emailModelChange (data) {
      this.emailModel = data
    },
    passModelChange (data) {
      this.passModel = data
    },
    auth () {
      this.login({
        emailModel: this.emailModel,
        passModel: this.passModel
      })
      if (this.isAuth) {
        this.$router.replace('/dashboard/profile')
      }
    }
  },
  computed: {
    ...mapState(['isAuth', 'validation'])
  },
  components: {
    TextInput,
    PasswordInput,
    AppButton,
    AppHeader
  }
}
</script>
